<template>
  <div class="wrapper">
    <v-container>
      <v-row align="center">
        <v-col md="4" style="margin-left: 120px;">
          <v-row>
            <v-col align="center">
              <v-img class="shrink mt-1 mb-10" src="@/images/loginlogo2.png" />
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field dense hide-details label="Email (*)" placeholder="mail@domain.com" outlined v-model="email" ref="email" id="email"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field dense hide-details type="password" label="Password (*)" placeholder="Min. 6 character" outlined v-model="password" ref="password"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-checkbox
                class="shrink mr-0 mt-0"
                v-model="rememberMe"
                label="Remember me"
                color="indigo darken-3"
                hide-details
              ></v-checkbox>
            </v-col>
            <v-col align="right">
              <router-link to="/forget-password"><strong class="blue--text text--lighten-1">Forget password?</strong></router-link
              >
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-btn depressed color="primary" width="100%" @click="setLogin"> Login </v-btn>
            </v-col>
          </v-row>
          <v-row>
            <v-col align="left">
              Not registered yet?
              <router-link to="/create-account"
                ><strong class="blue--text text--lighten-1">Create an Account</strong></router-link
              >
            </v-col>
          </v-row>
        </v-col>
        <v-col md="6">
          <v-img class="shrink mt-1" src="@/images/login.png" max-width="100%" />
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<style scoped>
.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
</style>

<script>
// eslint-disable-next-line no-unused-vars
import axios from "axios";
// eslint-disable-next-line no-unused-vars
import $ from "jquery";
// eslint-disable-next-line no-unused-vars
import VueCookies from "vue-cookies";
// eslint-disable-next-line no-unused-vars
import common from '@/common';

export default {
  data: () => ({
    email: "",
    password: "",
    rememberMe: false,
  }),
  mounted() {
    $("#top_header").hide();
    //Remember me Check
    if (VueCookies.isKey("rememberMe") && VueCookies.get("rememberMe") == "Y") {
      this.email = VueCookies.get("rememberMeEmail");
      this.rememberMe = true;
    }
  },
  methods: {
    setLogin() {
      if (!/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(this.email)) {
        this.$alert(common.messages.email, "", "info").then(() => {
          this.$refs.email.focus();
        });
        return false;
      }
      if (!/^[\S]{6,}$/.test(this.password)) {
        this.$alert(common.messages.password, "", "info").then(() => {
          this.$refs.password.focus();
        });
        return false;
      }
      if (this.rememberMe) {
        VueCookies.set("rememberMe", "Y");
        VueCookies.set("rememberMeEmail", this.email);
      } else {
        VueCookies.set("rememberMe", "N");
      }
      VueCookies.set("companyName", "복지단");
      VueCookies.set("companyRegisterNo", "154-44514-21");
      this.$session.set("loginId", "jidan");
      this.$session.set("loginCode", "01");
      this.$session.set("loginEmail", "test@test.co.kr");
      this.$session.set("loginCompanyName", "복지단");
      this.$session.set("authorization", "gemplatform");
      this.$router.push("home"); 
      //location.href="/home";
    },
  },
};
</script>
